import "./intro.scss"
import { init } from 'ityped'
import { useEffect, useRef } from "react"

export default function Intro() {

   const textRef = useRef(); 

   useEffect(() => {
    init(textRef.current, 
        { showCursor: true, 
          backDelay: 1500,
          backSpeed: 60,
          strings: ['Full-Stack Web', 'Downstream Process', 'Music']
         })
   },[]);


    return (
        <div className='intro' id='intro'>
            <div className="left">
                <div className="imageContainer">
                    <img src="assets/self2.png" alt="" />
                </div>
            </div>
            <div className="right">
                <div className="wrapper">
                    <h2>Welcome!</h2>
                    <h1>Jared Minaga</h1>
                    
                    <h3><span ref={textRef}></span> Developer</h3>
                </div>
                <a href="#portfolio"></a>
            </div>
        </div>
    )
}
